:root {
  --background: #0A0A0B;
  --foreground: #E6E6E6;
  --card-bg: #0F1113;
  --card-border: rgba(255,255,255,0.04);
  --panel-muted: rgba(255,255,255,0.02);
  --radius-sm: 8px;
  --radius-md: 10px;
  --radius-lg: 14px;
  --shadow-floating: 0 8px 30px rgba(0,0,0,0.6);
  --purple-light: #A78BFA;
  --purple-base: #8B5CF6;
  --purple-dark: #6D28D9;
  --purple-darker: #4C1D95;
  --win-color: #10B981;
  --loss-color: #EF4444;
  --neutral-color: #9CA3AF;
  --blue-accent: #3B82F6;
  --cyan-accent: #06B6D4;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html, body {
  width: 100%;
  height: 100%;
  background-color: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans);
  overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--card-bg);
}
::-webkit-scrollbar-thumb {
  background: var(--purple-base);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--purple-light);
}
.gradient-purple {
  background: linear-gradient(135deg, var(--purple-base) 0%, var(--purple-dark) 100%);
}
.gradient-purple-accent {
  background: linear-gradient(135deg, var(--purple-light) 0%, var(--blue-accent) 100%);
}
.gradient-text {
  background: linear-gradient(135deg, var(--purple-light) 0%, var(--cyan-accent) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.card {
  background-color: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  padding: 20px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.card:hover {
  border-color: var(--purple-base);
  box-shadow: 0 8px 32px rgba(139, 92, 246, 0.15);
  transform: translateY(-2px);
}
.btn-primary {
  background: linear-gradient(135deg, var(--purple-base) 0%, var(--purple-dark) 100%);
  color: white;
  padding: 12px 24px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}
.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(139, 92, 246, 0.4);
}
.btn-primary:active {
  transform: translateY(0);
}
input[type="text"],
input[type="number"],
input[type="email"],
input[type="password"],
input[type="date"],
input[type="time"],
textarea,
select {
  background-color: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 8px;
  padding: 10px 12px;
  color: var(--foreground);
  transition: all 0.3s ease;
  font-size: 14px;
}
input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: var(--purple-base);
  box-shadow: 0 0 0 3px rgba(139, 92, 246, 0.1);
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(139, 92, 246, 0.7);
  }
  50% {
    box-shadow: 0 0 0 10px rgba(139, 92, 246, 0);
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.animate-fadeIn {
  animation: fadeIn 0.5s ease-in;
}
.animate-slideUp {
  animation: slideUp 0.5s ease-out;
}
.animate-pulse-glow {
  animation: pulse-glow 2s infinite;
}
.trader-background {
  position: fixed;
  inset: 0;
  z-index: -1;
  pointer-events: none;
  background-image: radial-gradient(ellipse at center, rgba(139,92,246,0.04), transparent 25%), linear-gradient(120deg, rgba(10,10,11,0.85), rgba(20,18,20,0.9));
  opacity: 0.95;
}
.trader-lines {
  position: absolute;
  inset: 0;
  background-image: repeating-linear-gradient(135deg, rgba(139,92,246,0.03) 0 2px, transparent 2px 24px);
  animation: drift 18s linear infinite;
  mix-blend-mode: overlay;
}
@keyframes drift {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 400px 400px;
  }
}
